400;500;600;700&display=swap");
@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

.bs--wrap {
  width: 1200px;
  position: relative;
  margin: 0 auto;
}

.bs--wrap .bs--title {
  text-align: center;
  font-family: var(--font1);
  text-transform: uppercase;
  color: #fff;
  padding: 300px 0;
  padding-bottom: 50px;
  font-size: 170px;
  position: relative;
}

.bs--wrap .bs--links {
  font-family: var(--font2);
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #fff;
  text-align: center;
  margin-bottom: 300px;
}

.bs--wrap .bs--links a {
  display: inline-block;
  margin: 0 16px;
  transition: 0.25s all ease;
}

.bs--wrap .bs--links a:hover {
  color: var(--hx3);
}

.bs--wrap .bs--content {
  background: #fff var(--gradient3);
  width: 100%;
  padding: 75px;
}